body {
  background-color: #D7D7D7; /* Set the background color of the page */
}

.skills-page {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  width: 100%;
  padding-top: 10%;
}

.section {
  width: 20%;
  border: 8px solid #011526;
  border-radius: 10px; /* Added border radius for rounded corners */
  background: #D7D7D7;
  margin: 20px 0;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  padding: 20px;
}

.section h2 {
  margin: 20px 0;
}

.logo-grid {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  margin: 6% 0;
  width: 75%; /* Match the width of the .section class */
}

.logo-row {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  margin: 3% 0;
  width: 100%;
}